import { useLocation, useParams, useSearchParams } from "react-router-dom";
import RectangleTabCard from "../../Ui/Tab/Rectangle/RectangleTabCard";
import RectangleTabWrapper from "../../Ui/Tab/Rectangle/RectangleTabWrapper";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import SquareTabWrapper from "../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../Ui/Tab/Square/SquareTabCard";
import FeesStructure from "../../UserStaffMember/FinanceManager/FeesStructure/FeesStructure";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { memo } from "react";

const InstituteFinanceManagerProfile = ({
  instituteId,
  fid,
  categoryCount,
  masterCount,
  tabManage,
}) => {
  const params = useParams();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  let measure = document.getElementById("562nbhgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    width = `${Math.floor((measure - 32 - 31 * 4) / 5)}px`;
    height = width;
  }
  return (
    <>
      <UserMemberTabsWrapper>
        <RectangleTabWrapper>
          {tabList.map((tab) => (
            <RectangleTabCard
              key={tab?.id}
              label={tab.label}
              isLink
              isActive={
                tab.id === "menu" && !searchParams.get("type")
                  ? true
                  : searchParams.get("type") === tab.id
                  ? true
                  : false
              }
              parentState={getQuery.state}
              url={`/q/${params.username}/institute/member/finance${tab.urlKey}`}
              isAuthorized
            />
          ))}
        </RectangleTabWrapper>
        {!searchParams.get("type") ? (
          <SquareTabWrapper dynamicId="562nbhgh">
            <SquareTabCard
              label="bank_details"
              iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
              url={`/q/${params.username}/institute/member/finance?type=bank`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="bank_details"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="pending_fees"
              iconUrl={`${assestsUserFinanceUrl}/pending.svg`}
              url={`/q/${params.username}/institute/member/finance?type=pending-fee`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="pending_fees"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />

            <SquareTabCard
              label="deposits"
              iconUrl={`${assestsUserFinanceUrl}/deposit.svg`}
              url={`/q/${params.username}/institute/member/finance?type=deposit`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="deposits"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="incomes"
              iconUrl={`${assestsUserFinanceUrl}/income.svg`}
              url={`/q/${params.username}/institute/member/finance?type=income`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="incomes"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="expenses"
              iconUrl={`${assestsUserFinanceUrl}/expense.svg`}
              url={`/q/${params.username}/institute/member/finance?type=expense`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="expenses"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="scholarsips"
              iconUrl={`${assestsUserFinanceUrl}/scholarship.svg`}
              url={`/q/${params.username}/institute/member/finance?type=scholarship`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="scholarships"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="exemption_and_unrecovered"
              iconUrl={`${assestsUserFinanceUrl}/exemption.svg`}
              url={`/q/${params.username}/institute/member/finance?type=exemption`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="exemption"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="transaction_history"
              iconUrl={`${assestsUserFinanceUrl}/history.svg`}
              url={`/q/${params.username}/institute/member/finance?type=history`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="transaction_history"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="data_export"
              iconUrl={`${assestsUserFinanceUrl}/export.svg`}
              url={`/q/${params.username}/institute/member/finance?type=export`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="data_export"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="finance_moderators"
              iconUrl={`${assestsUserFinanceUrl}/moderator.svg`}
              url={`/q/${params.username}/institute/member/finance?type=moderator`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="finance_mods"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="internal_fees"
              iconUrl={`${assestsUserFinanceUrl}/internal-fees.svg`}
              url={`/q/${params.username}/institute/member/finance?type=internal-fees`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ internal_fees: true }}
              accessKey="internal_fees"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="student_statitics"
              iconUrl={`${assestsUserFinanceUrl}/statitics.svg`}
              url={`/q/${params.username}/institute/member/finance?type=student-statistics`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ student_statitics: true }}
              accessKey="student_statitics"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="fees_statitics"
              iconUrl={`${assestsUserFinanceUrl}/statitics.svg`}
              url={`/q/${params.username}/institute/member/finance?type=fees-statistics`}
              state={getQuery.state}
              isAuthorized
              isVisible={tabManage ?? {}}
              accessKey="fee_statistics"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
            <SquareTabCard
              label="tab_controller"
              iconUrl={`${assestsUserFinanceUrl}/controller.svg`}
              url={`/q/${params.username}/institute/member/finance?type=tab`}
              state={getQuery.state}
              isAuthorized
              isVisible={{ tab_controller: true }}
              accessKey="tab_controller"
              customStyleContainer={{
                height: height,
                width: width,
              }}
            />
          </SquareTabWrapper>
        ) : searchParams.get("type") === "fees" ? (
          <FeesStructure
            fid={fid}
            instituteId={instituteId}
            masterCount={masterCount}
            categoryCount={categoryCount}
          />
        ) : null}
      </UserMemberTabsWrapper>
    </>
  );
};

export default memo(InstituteFinanceManagerProfile);

const tabList = [
  {
    label: "menu",
    id: "menu",
    urlKey: "",
  },
  {
    label: "fees_structure",
    id: "fees",
    urlKey: "?type=fees",
  },
  {
    label: "account",
    id: "account",
    urlKey: "?type=account",
  },
];
