import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import InstituteSideBg from "../Config/InstituteSideBg";
import InstituteSideCard from "../Config/InstituteSideCard";
import { Suspense, lazy, memo, useMemo, useState } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
const UpdateInstituteFinanceManager = lazy(() =>
  import("./UpdateInstituteFinanceManager")
);

const InstituteFinanceManagerSide = ({ financeDashboardMaster }) => {
  const { t } = useTranslation();
  const [openMenu, setOpenMenu] = useState(false);

  const getCashBalance = useMemo(() => {
    let cash =
      financeDashboardMaster?.financeCashBalance ??
      0 + financeDashboardMaster?.financeSubmitBalance ??
      0 + financeDashboardMaster?.financeIncomeCashBalance ??
      0;

    let exp = financeDashboardMaster?.financeExpenseCashBalance ?? 0;
    if (cash > exp) cash -= exp;
    return cash;
  }, [
    financeDashboardMaster?.financeCashBalance,
    financeDashboardMaster?.financeSubmitBalance,
    financeDashboardMaster?.financeIncomeCashBalance,
    financeDashboardMaster?.financeExpenseCashBalance,
  ]);
  const getBankBalance = useMemo(() => {
    let bank =
      financeDashboardMaster?.financeBankBalance ??
      0 + financeDashboardMaster?.financeIncomeBankBalance ??
      0;
    let exp = financeDashboardMaster?.financeExpenseBankBalance ?? 0;
    if (bank > exp) bank -= exp;
    return bank;
  }, [
    financeDashboardMaster?.financeBankBalance,
    financeDashboardMaster?.financeIncomeBankBalance,
    financeDashboardMaster?.financeExpenseBankBalance,
  ]);
  const onOpenMenu = () => {
    setOpenMenu((pre) => !pre);
  };

  // console.info("how many tmes");
  return (
    <>
      <div>
        <GoBack
          customStyle={{
            width: "100%",
            marginTop: "0",
          }}
        />
        <InstituteSideBg
          avatar={
            financeDashboardMaster?.cover
              ? `${imageShowUrl}/${financeDashboardMaster?.cover}`
              : `${assestsUserFinanceUrl}/cover.svg`
          }
          onAction={onOpenMenu}
        />
        <InstituteSideCard
          title={`${
            financeDashboardMaster?.financeHead?.staffFirstName || ""
          } ${
            financeDashboardMaster?.financeHead?.staffMiddleName
              ? financeDashboardMaster?.financeHead?.staffMiddleName
              : ""
          } ${financeDashboardMaster?.financeHead?.staffLastName || ""}`}
          subtitle={t("finace_manager")}
          customStyleContainer={{
            borderRadius: "18px 18px 0px 0px",
            border: "none",
            marginTop: "-1.49vw",
          }}
        />
        <InstituteSideCard title={t("cash")} subtitle={t("cash_balance")}>
          <h6>
            {t("rs_only")}
            {onNumberFormat(getCashBalance ?? 0)}
          </h6>
        </InstituteSideCard>
        <InstituteSideCard title={t("bank")} subtitle={t("bank_balance")}>
          <h6>
            {t("rs_only")}
            {onNumberFormat(getBankBalance ?? 0)}
          </h6>
        </InstituteSideCard>
        <InstituteSideCard
          title={t("due")}
          subtitle={t("due_from_qviple")}
          customStyleContainer={{
            borderRadius: "0 0 12px 12px",
            borderBottom: "0.4px solid rgba(0, 0, 0, 0.15)",
          }}
        >
          <h6>
            {t("rs_only")}

            {onNumberFormat(
              financeDashboardMaster?.institute?.adminRepayAmount ?? 0
            )}
          </h6>
        </InstituteSideCard>
      </div>
      {openMenu && (
        <Suspense fallback={<QvipleLoader />}>
          <UpdateInstituteFinanceManager
            financeHead={financeDashboardMaster?.financeHead}
            onClose={onOpenMenu}
          />
        </Suspense>
      )}
    </>
  );
};

export default memo(InstituteFinanceManagerSide);

const onNumberFormat = (val) => {
  return val?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") || 0;
};
